/* App.css */

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */
    padding: 5%;
    /* height: 100vh; */
}

.image-upload {
    display: flex;
    flex-direction: column;
    width: 80%;
    align-items: center;
}
.mb10 {
    margin-bottom: 10px;
}
.fr {
    display: flex;
    justify-content: space-between;
    width: 50%;
}
.chooseFile {
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    flex-wrap: nowrap;
    width: 350px;
    justify-content: space-around;
}

.fileContainer {
    margin-top: 20px;
}

.fileInput {
    margin-bottom: 10px;
}

.image-show {
    position: relative;
}
canvas {
    width: 100%;
    height: 100%;
    max-width: 500px !important;
    max-height: 500px !important;
    position: absolute;
    left: -9999px;
    top: -9999px;
}
img {
    width: 90%;
    height: 90%;
    max-width: 500px;
    max-height: 500px;
    margin-top: 50px;
    object-fit: cover;
    overflow: visible;
}

.text-overlay {
    position: absolute;
    bottom: 70px; /* 아래 여백 조정 */
    left: 20px; /* 왼쪽 여백 조정 */
    color: #676767; /* 텍스트 색상 */
    width: 150px !important;
    background-color: #fff;
    font-size: 1rem; /* 텍스트 크기 */
    line-height: 25px;
    font-family: Arial, sans-serif; /* 텍스트 폰트 */
    /* box-shadow: 2px 2px 2px rgba(185, 184, 184, 0.653);  */
}
.text-table td {
    height: 5px !important;
}
.text-table td:first-child {
    width: 40px !important;
}
